import React, { Component } from 'react';
import './App.css';
import {
  CLIENT_ID,
  CLIENT_SECRET,
  ACCESS_TOKEN,
  USERNAME,
  AVATAR,
  PROXY,
  STATUS,
  OWNER,
  REPO,
  BG1,
  BG2,
  WECHAT,
  MUSIC,
  QRCODE,
  TIPS1_TIME,
  TIPS2_TIME,
  PER_PAGE,
  ISSUE_NUM,
  YEAR_START,
  YEAR_END,
  SERVER,
  EMAIL,
} from './utils/constant';
import { queryParse, axiosJSON, timeout } from './utils/helper';
import Octokit from '@octokit/rest';
import Alert from 'antd/lib/alert';
import Button from 'antd/lib/button';
import Spin from 'antd/lib/spin';
import { ReactComponent as GitHub } from './icon/githubWhite.svg';
import Slide from './components/Slide';
import analysisSingle from './utils/analysisSingle';
import analysisInfo from './utils/analysisInfo';

class App extends Component {
  constructor(props) {
    super(props);

    this.state = {
      failed: false,
      loading: true,
      firstPage: true,
      status: '...',
      requestNums: 0,
      fineshedRequest: 0,
      failedRequest: 0,
    };
    this.octokit = new Octokit();
    this.repos = [];
    this.info = {};
    this.existRepos = {};

    this.run();
  }

  componentDidMount() {
    this.timer1 = setInterval(() => this.tips1(), TIPS1_TIME);
    this.timer2 = setInterval(() => this.tips2(), TIPS2_TIME);
  }

  componentWillUnmount() {
    clearInterval(this.timer1);
    clearInterval(this.timer2);
  }

  tips1() {
    if (this.state.fineshedRequest === this.state.requestNums) {
      clearInterval(this.timer1);
      clearInterval(this.timer2);
    } else {
      this.setState({
        status: `${this.state.fineshedRequest}/${this.state.requestNums}${this.state.failedRequest}`,
      });
    }
  }

  tips2() {
    this.setState({
      status: ``,
    });
  }

  // 
  run = async () => {
    const query = queryParse();
    this.fetchAssets();
    // 
    if (query.isUpdate) {
      this.authenticate();
      await this.calc();
      console.log(this.repos);
      console.log(this.info);
      console.log(this.existRepos);
      this.setState({ loading: false, firstPage: false });
    }
    // username
    if (query.username) {
      this.setState({ status: '', requestNums: this.state.requestNums + 1 });
      const isExist = await this.getInfo(query.username);
      // 
      if (!isExist) {
        this.setState({ status: '', fineshedRequest: this.state.fineshedRequest + 1 });
        //   API
        if (localStorage.getItem(USERNAME) && localStorage.getItem(USERNAME) === query.username) {
          this.authenticate();
          await this.calc();
          console.log(this.repos);
          console.log(this.info);
          console.log(this.existRepos);
          this.setState({ loading: false, firstPage: false });
        }
        // 
        else {
          this.setState({ loading: false });
        }
      }
      // 
      else {
        this.setState({ status: '' });
        this.setState({ loading: false, firstPage: false });
      }
    } else {
      // 
      if (localStorage.getItem(ACCESS_TOKEN) && localStorage.getItem(USERNAME)) {
        // 
        window.location.href = `/?username=${localStorage.getItem(USERNAME)}`;
      }
      // code
      else if (query.code) {
        // localStoragetoken
        await this.fetchToken(query.code);
        // TokenOctokit
        this.authenticate();
        // localStorageusernameavatar
        await this.fetchUser();
        window.location.href = `/?username=${localStorage.getItem(USERNAME)}`;
      }
      // 
      else {
        this.state.loading = false;
      }
    }
  };

  // 
  calc = async () => {
    this.repos = [];
    this.info = {};
    this.existRepos = [];
    // Issue Star repo commit
    await this.fetchInfo();
    this.setState({ status: '...' });
    analysisSingle(this.repos);
    this.setState({ status: '...' });
    analysisInfo(this.info, this.repos);
    // localStorage.setItem(INFO, JSON.stringify(this.info));
    const req = {
      username: localStorage.getItem(USERNAME),
      avatar: localStorage.getItem(AVATAR),
      token: localStorage.getItem(ACCESS_TOKEN),
      email: localStorage.getItem(EMAIL),
      info: JSON.stringify(this.info),
      repo: JSON.stringify(this.repos),
    };
    await this.addInfo(req);
    this.setState({ status: '' });
  };

  fetchAssets = () => {
    // 
    this.fetchImage(BG1);
    this.fetchImage(BG2);
    this.fetchImage(WECHAT);
    this.fetchImage(QRCODE);
    this.fetchAudio(MUSIC);
  };

  fetchImage = url => {
    return new Promise(function(resolve, reject) {
      var img = new Image();
      img.src = url;
      img.onload = function() {
        resolve();
      };
      img.onerror = function() {
        reject();
      };
    });
  };

  fetchAudio = url => {
    return new Promise(function(resolve, reject) {
      var audio = new Audio();
      audio.src = url;
      audio.onload = function() {
        resolve();
      };
      audio.onerror = function() {
        reject();
      };
    });
  };

  // GitHubusername
  fetchComments = async username => {
    let comments;
    let commentsExist = false;
    let commentsPage = 1;
    do {
      this.state.status = '';
      this.state.requestNums = this.state.requestNums + 1;
      comments = await timeout(
        this.octokit.issues.listComments({
          owner: OWNER,
          repo: REPO,
          number: ISSUE_NUM,
          per_page: PER_PAGE,
          page: commentsPage,
        })
      );
      if (!comments || comments.status !== STATUS.OK) {
        this.setState({ failedRequest: this.state.failedRequest + 1 });
        return;
      }
      for (const comment of comments.data) {
        if (comment.user.login === username) {
          try {
            this.info = JSON.parse(comment.body); //
          } catch (e) {
            this.setState({ failed: true });
          }
          if (this.info && this.info.specialDay.date !== '') {
            this.info.specialDay.date = new Date(this.info.specialDay.date);
          }
          if (this.info && this.info.latestDay.date !== '') {
            this.info.latestDay.date = new Date(this.info.latestDay.date);
          }
          commentsExist = true;
          break;
        }
      }
      commentsPage++;
    } while (comments.data.length === PER_PAGE && !commentsExist);
    return commentsExist;
  };

  fetchUser = async () => {
    this.setState({ status: '', requestNums: this.state.requestNums + 1 });
    // 
    const userInfo = await timeout(this.octokit.users.getAuthenticated());
    if (!userInfo || userInfo.status !== STATUS.OK) {
      this.setState({ failedRequest: this.state.failedRequest + 1, status: '' });
      return;
    }
    localStorage.setItem(USERNAME, userInfo.data.login);
    localStorage.setItem(AVATAR, userInfo.data.avatar_url);
    localStorage.setItem(EMAIL, userInfo.data.email);
    this.setState({ status: '', fineshedRequest: this.state.fineshedRequest + 1 });
  };

  // fetchIssues, fetchStars, fetchRepos, fetchCommits
  fetchInfo = async () => {
    const promiseArr = [];
    // issue
    promiseArr.push(this.fetchIssues());
    // star
    promiseArr.push(this.fetchStars());
    // event
    promiseArr.push(this.fetchEvents());

    /**  2  repo **/
    // watchrepo
    promiseArr.push(this.fetchWatchRepo());
    // repo
    promiseArr.push(this.fetchSelfRepo());

    // 
    await Promise.all(promiseArr);
  };

  fetchEvents = async () => {
    let events;
    let eventOver = false;
    let eventPage = 1;
    const hashObject = {};
    this.info.eventNums = 0;
    do {
      this.setState({ status: 'Event', requestNums: this.state.requestNums + 1 });
      events = await timeout(
        this.octokit.activity.listPublicEventsForUser({
          username: localStorage.getItem(USERNAME),
          per_page: PER_PAGE,
          page: eventPage,
        })
      );
      if (!events || events.status !== STATUS.OK) {
        this.setState({ failedRequest: this.state.failedRequest + 1 });
        return;
      }
      // 
      for (const event of events.data) {
        const created_at = new Date(event.created_at);
        // 2019
        if (created_at.getTime() <= YEAR_START.getTime()) {
          eventOver = true;
          break;
        }
        // 2019
        if (created_at.getTime() >= YEAR_END.getTime()) {
          continue;
        }
        const key = created_at.getTime();
        if (key in hashObject) {
          continue;
        } else {
          hashObject[key] = true;
        }
      }
      this.info.eventNums += events.data.length;
      this.setState({ status: `${eventPage}Event`, fineshedRequest: this.state.fineshedRequest + 1 });
      eventPage++;
    } while (events.data.length === PER_PAGE && !eventOver);
    this.info.eventNums = Object.keys(hashObject).length;
    this.setState({ status: 'Event' });
  };

  //  
  fetchWatchRepo = async () => {
    const promiseArr = [];
    // 
    let repos;
    let repoPage = 1;
    do {
      this.setState({ status: '', requestNums: this.state.requestNums + 1 });
      repos = await timeout(this.octokit.activity.listWatchedReposForAuthenticatedUser({ per_page: PER_PAGE, page: repoPage }));
      if (!repos || repos.status !== STATUS.OK) {
        this.setState({ failedRequest: this.state.failedRequest + 1 });
        return;
      }
      // 
      for (const repo of repos.data) {
        const created_at = new Date(repo.created_at);
        const pushed_at = new Date(repo.pushed_at);
        // 2019
        if (created_at.getTime() >= YEAR_END.getTime()) {
          continue;
        }
        // 2019  push20192019
        if (
          created_at.getTime() < YEAR_END.getTime() &&
          pushed_at.getTime() > YEAR_START.getTime() &&
          this.existRepos[repo.name] === undefined
        ) {
          this.existRepos[repo.name] = true;
          // Promise
          promiseArr.push(this.fetchCommits(repo));
        }
      }
      this.setState({ status: `${repoPage}`, fineshedRequest: this.state.fineshedRequest + 1 });
      repoPage++;
    } while (repos.data.length === PER_PAGE);
    // 
    await Promise.all(promiseArr);
    this.setState({ status: `` });
  };

  //  
  fetchSelfRepo = async () => {
    const promiseArr = [];
    // 
    let repos;
    let repoPage = 1;
    let repoOver = false;
    do {
      this.setState({ status: '', requestNums: this.state.requestNums + 1 });
      repos = await timeout(this.octokit.repos.list({ visibility: 'all', sort: 'pushed', per_page: PER_PAGE, page: repoPage }));
      if (!repos || repos.status !== STATUS.OK) {
        this.setState({ failedRequest: this.state.failedRequest + 1 });
        return;
      }
      // 
      for (const repo of repos.data) {
        const created_at = new Date(repo.created_at);
        const pushed_at = new Date(repo.pushed_at);
        // push2019push
        if (pushed_at.getTime() <= YEAR_START.getTime()) {
          repoOver = true;
          break;
        }
        // 2020
        if (created_at.getTime() >= YEAR_END.getTime()) {
          continue;
        }
        // 2020  push20192018
        if (
          created_at.getTime() < YEAR_END.getTime() &&
          pushed_at.getTime() > YEAR_START.getTime() &&
          this.existRepos[repo.name] === undefined
        ) {
          this.existRepos[repo.name] = true;
          // Promise
          promiseArr.push(this.fetchCommits(repo));
        }
      }
      this.setState({ status: `${repoPage}`, fineshedRequest: this.state.fineshedRequest + 1 });
      repoPage++;
    } while (repos.data.length === PER_PAGE && !repoOver);
    // 
    await Promise.all(promiseArr);
    this.setState({ status: `` });
  };

  // issue
  fetchIssues = async () => {
    let issues;
    let issuePage = 1;
    this.info.issueNums = 0;
    do {
      this.setState({ status: 'Issue', requestNums: this.state.requestNums + 1 });
      issues = await timeout(
        this.octokit.issues.list({
          filter: 'all',
          state: 'all',
          since: YEAR_START.toISOString(),
          per_page: PER_PAGE,
          page: issuePage,
        })
      );
      if (!issues || issues.status !== STATUS.OK) {
        this.setState({ failedRequest: this.state.failedRequest + 1 });
        return;
      }
      this.info.issueNums += issues.data.length;
      this.setState({ status: `${issuePage}Issue`, fineshedRequest: this.state.fineshedRequest + 1 });
      issuePage++;
    } while (issues.data.length === PER_PAGE);
    this.setState({ status: 'Issue' });
  };

  // star
  fetchStars = async () => {
    let stars;
    let starPage = 1;
    this.info.starNums = 0;
    do {
      this.setState({ status: 'Star', requestNums: this.state.requestNums + 1 });
      stars = await timeout(
        this.octokit.activity.listReposStarredByAuthenticatedUser({ sort: 'created', per_page: PER_PAGE, page: starPage })
      );
      if (!stars || stars.status !== STATUS.OK) {
        this.setState({ failedRequest: this.state.failedRequest + 1 });
        return;
      }
      this.info.starNums += stars.data.length;
      this.setState({ status: `${starPage}Star`, fineshedRequest: this.state.fineshedRequest + 1 });
      starPage++;
    } while (stars.data.length === PER_PAGE);
    this.setState({ status: 'Star' });
  };

  // 
  fetchCommits = async repo => {
    const statsRepo = await this.fetchStats(repo);
    if (!statsRepo) {
      return;
    }
    let commits;
    let commitPage = 1;
    let commitOver = false;
    // 2019-2020
    do {
      this.setState({ status: `${repo.name}Commit`, requestNums: this.state.requestNums + 1 });
      commits = await timeout(
        this.octokit.repos.listCommits({
          owner: repo.owner.login,
          repo: repo.name,
          author: localStorage.getItem(USERNAME),
          per_page: PER_PAGE,
          page: commitPage,
          since: YEAR_START.toISOString(),
          until: YEAR_END.toISOString(),
        })
      );
      if (!commits || commits.status !== STATUS.OK) {
        this.setState({ failedRequest: this.state.failedRequest + 1 });
        return;
      }
      for (const commit of commits.data) {
        const currentDate = new Date(commit.commit.committer.date);
        // 2019
        if (currentDate.getTime() <= YEAR_START.getTime()) {
          commitOver = true;
          break;
        }
        // 2019
        if (currentDate.getTime() >= YEAR_END.getTime()) {
          continue;
        }
        //   20192020
        if (
          (commit.committer && commit.committer.login === localStorage.getItem(USERNAME)) ||
          (commit.author && commit.author.login === localStorage.getItem(USERNAME)) ||
          (commit.commit.committer && commit.commit.committer.name === localStorage.getItem(USERNAME)) ||
          (commit.commit.author && commit.commit.author.name === localStorage.getItem(USERNAME))
        ) {
          if(commit.commit.committer && commit.commit.committer.date) {
            console.log(commit)
            console.log(statsRepo)
            statsRepo.commitTime.push(commit.commit.committer.date);
          } else if(commit.commit.author && commit.commit.author.date) {
            statsRepo.commitTime.push(commit.commit.author.date);
          }
          statsRepo.commitSha.push(commit.sha);
        }
      }
      this.setState({ status: `${repo.name}${commitPage}Commit`, fineshedRequest: this.state.fineshedRequest + 1 });
      commitPage++;
    } while (commits.data.length === PER_PAGE && !commitOver);
    this.setState({ status: `${repo.name}Commit` });
  };

  fetchStats = async repo => {
    this.setState({ status: '', requestNums: this.state.requestNums + 1 });
    const stats = await timeout(
      this.octokit.repos.getContributorsStats({
        owner: repo.owner.login,
        repo: repo.name,
      })
    );
    this.setState({ status: `${repo.name}`, fineshedRequest: this.state.fineshedRequest + 1 });

    if (!stats || stats.status !== STATUS.OK) {
      this.setState({ failedRequest: this.state.failedRequest + 1 });
      return;
    }
    const detail = stats.data.filter(item => item.author.login === localStorage.getItem(USERNAME))[0];
    if (detail === undefined) {
      return null;
    }
    // 
    const { total, weeks } = detail;
    if (total > 0) {
      // 
      const dateTime = new Date(YEAR_START).getTime();
      const timestamp = Math.floor(dateTime / 1000);
      const weeksThisYear = weeks.filter(week => week.w > timestamp);
      if(weeksThisYear === undefined) {
        return null;
      }
      const statsRepo = {
        repo: repo.name,
        owner: repo.owner.login,
        language: repo.language,
        commitTime: [],
        commitSha: [],
        addLines: 0,
        deleteLines: 0,
        totalLines: 0,
      };
      statsRepo.addLines = weeks.map(week => week.a).reduce((pre, cur) => pre + cur);
      statsRepo.deleteLines = weeks.map(week => week.d).reduce((pre, cur) => pre + cur);
      statsRepo.totalLines = statsRepo.addLines + statsRepo.deleteLines;

      // 2019
      this.repos.push(statsRepo);
      return statsRepo;
    }
    return null;
  };

  fetchToken = async code => {
    this.state.status = `Token`;
    this.state.requestNums = this.state.requestNums + 1;
    const res = await axiosJSON.post(PROXY, {
      code,
      client_id: CLIENT_ID,
      client_secret: CLIENT_SECRET,
    });
    if (res.status !== STATUS.OK) {
      this.setState({ failed: true });
      return;
    }
    localStorage.setItem(ACCESS_TOKEN, res.data.access_token);
    this.setState({ status: `Token` });
  };

  authenticate = () => {
    this.octokit.authenticate({
      type: 'oauth',
      token: localStorage.getItem(ACCESS_TOKEN),
    });
  };

  login = () => {
    this.setState({ loading: true });
    window.location.href = `https://github.com/login/oauth/authorize?client_id=${CLIENT_ID}&scope=public_repo`;
  };

  onClose = () => {
    this.setState({ failed: false });
  };

  getInfo = async username => {
    let isExist = false;
    const res = await axiosJSON.get(SERVER + '/users/' + username);
    const { data } = res.data;
    if (data && data.username === username) {
      try {
        this.info = JSON.parse(data.info); //
      } catch (e) {
        this.setState({ failed: true });
      }
      if (this.info.specialDay.date !== '') {
        this.info.specialDay.date = new Date(this.info.specialDay.date);
      }
      if (this.info.latestDay.date !== '') {
        this.info.latestDay.date = new Date(this.info.latestDay.date);
      }
      isExist = true;
    }
    return isExist;
  };

  addInfo = async info => {
    const res = await axiosJSON.post(SERVER + '/users', info);
    if (res) {
      this.setState({ status: '' });
    }
  };

  render() {
    let firstPage = null;
    const styles = {
      firstPage: {
        backgroundImage: `url(http://githubreport.oss-cn-beijing.aliyuncs.com/bg1.jpg)`,
        backgroundSize: '100%',
      },
    };
    if (this.state.loading) {
      firstPage = <Spin className="spin" size="large" tip={this.state.status} />;
    } else {
      firstPage = (
        <div className="header">
          <p>2019GitHub</p>
          <p></p>
          <Button className="login" type="primary" onClick={this.login}>
            <GitHub className="github" />
            
          </Button>
        </div>
      );
    }
    return (
      <div className="App">
        {this.state.firstPage ? (
          <div className="firstPage" style={styles.firstPage}>
            {this.state.failed ? (
              <Alert
                className="failed"
                message="GitHub"
                type="error"
                closable
                afterClose={this.onClose}
                banner
              />
            ) : null}
            {firstPage}
          </div>
        ) : (
          <Slide info={this.info} octokit={this.octokit} />
        )}
      </div>
    );
  }
}

export default App;
