import React, { Component } from 'react';
import { fadeInUp } from 'react-animations';
import Radium, { StyleRoot } from 'radium';
import './Slide.css';
import { ReactComponent as Time } from '../icon/time.svg';

class Page1 extends Component {
  render() {
    const styles = {
      fadeInUp1s: {
        animation: 'x 1s',
        animationName: Radium.keyframes(fadeInUp, 'fadeInUp'),
      },
      fadeInUp1_5s: {
        animation: 'x 1.5s',
        animationName: Radium.keyframes(fadeInUp, 'fadeInUp'),
      },
      fadeInUp2s: {
        animation: 'x 2s',
        animationName: Radium.keyframes(fadeInUp, 'fadeInUp'),
      },
      fadeInUp2_5s: {
        animation: 'x 2.5s',
        animationName: Radium.keyframes(fadeInUp, 'fadeInUp'),
      },
      fadeInUp3s: {
        animation: 'x 3s',
        animationName: Radium.keyframes(fadeInUp, 'fadeInUp'),
      },
    };
    return (
      <StyleRoot>
        {this.props.info.likePeriod.name !== '' && this.props.page === 7 ? (
          <div className="page">
          <Time className="mb20"/>
            <p style={styles.fadeInUp1s}>
              <span className="stress">{this.props.info.likePeriod.name}</span>
            </p>
            <p style={styles.fadeInUp1_5s}>
              
              {this.props.info.likeWeekType.name === '' ? '' : ''}
              <span className="stress">{this.props.info.likeWeekType.name}</span>
            </p>
            <p style={styles.fadeInUp2s}>365</p>
            <p style={styles.fadeInUp2_5s}>
              <span className="stress">{this.props.info.likeWeekType.count}</span>{this.props.info.likeWeekType.name}
              
            </p>
          </div>
        ) : this.props.page === 7 ? (
          <div className="page">
          <Time className="mb20"/>
            <p style={styles.fadeInUp1s}></p>
            <p style={styles.fadeInUp1_5s}></p>
            <p style={styles.fadeInUp2s}></p>
          </div>
        ) : null}
      </StyleRoot>
    );
  }
}

export default Page1;
